@import "reset";
@import "vars";
.wrapper {
    width: 100%;
    max-width: 1080px;
    margin: 0 auto;
    padding-left: 1%;
    padding-right: 1%;
    // overflow-x: hidden;
    font-family: $font-primary;
}

.header {
    position: relative;
    padding-top: 25px;
    padding-bottom: 20px;
    background-color: $green;
    background-image: url("../img/header-bg.png"), url("../img/shadow-down.png");
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-size: 100% 100%, 100% 100%;
    &:after {
        content: "";
        position: absolute;
        bottom: -17px;
        left: 11%;
        width: 45%;
        height: 31px;
        background: url("../img/shine.png") no-repeat 100% 100%;
        z-index: 10;
    }
    &__title-box {
        position: relative;
        width: 350px;
        height: 80px;
        margin: 0 auto;
        padding-top: 3px;
        background-image: url("../img/text_box.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0px;
            width: 350px;
            height: 15px;
            opacity: 0.7;
            background: url("../img/title-shade.png") no-repeat 100% 100%;
        }
    }
    &__title {
        color: #fff;
        font-family: $font-primary;
        font-size: 30px;
        font-weight: 700;
        text-align: center;
        line-height: 1.2;
    }
    &__pollen {
        width: 24%;
        height: auto;
        position: absolute;
        bottom: -45px;
        z-index: 10;
    }
    @media only screen and (max-width: 768px) {
        .header__pollen,
        &:after {
            display: none!important;
        }
    }
    &__pollen--left {
        left: 1%;
    }
    &__pollen--right {
        right: 1%;
        -moz-transform: scaleX(-1);
        -o-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        filter: FlipH;
        -ms-filter: "FlipH";
    }
}

.menu {
    position: relative;
    min-height: 70px;
    background-color: $green;
    display: flex;
    justify-content: center;
    align-items: center;
    &:before {
        content: "";
        position: absolute;
        top: 10px;
        left: 0px;
        width: 100%;
        height: 15px;
        background: url("../img/shadow-under.png") no-repeat 100% 100%;
    }
    &__item {
        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
            background-size: contain;
        }
    }
    .menu__item--wide {
        padding-left: 10px;
        padding-right: 10px;
        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
        }
    }
    .is-active {
        color: #01453e;
    }
    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        min-width: 115px;
        min-height: 40px;
        line-height: 1.2;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
    &__dropdown {
        visibility: hidden;
        height: 0px;
        position: absolute;
        width: 150px;
        padding-top: 10px;
        padding-bottom: 5px;
        opacity: 0;
        background-color: $green;
        transition: all .3s ease-in-out;
    }
    .menu__item:hover .menu__dropdown {
        height: 120px;
        opacity: 1;
        visibility: visible;
        display: flex;
        flex-direction: column;
        z-index: 1000;
        .menu__item--dropdown {
            opacity: 1;
            transition-delay: 0.3s;
            transition: opacity .2s ease-in-out;
        }
    }
    .menu__item--dropdown {
        display: flex;
        padding: 7px 10px;
        color: #fff;
        opacity: 0;
        border-bottom: 1px solid #ccc;
        &:hover {
            background-color: darken($green, 10%)
        }
    }
}

// MAIN CONTENT
.owl-item {
    -webkit-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
}

//FOOTER
.footer {
    position: relative;
    height: 90px;
    padding: 10px;
    margin-bottom: 10px;
    background-color: $green;
    -webkit-box-shadow: 0px 10px 0px 0px #fba51a;
    -moz-box-shadow: 0px 10px 0px 0px #fba51a;
    box-shadow: 0px 10px 0px 0px #fba51a;
    border-bottom: 6px solid #dcdb88;
    &__icons-bar {
        text-align: center;
        margin: 0 auto;
    }
    &__references {
        position: absolute;
        right: 20px;
        bottom: 10px;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
}

// PAGE: WHAT ARE ALLERGY
.content {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-repeat: no-repeat;
    background-size: cover;
    &__cat {
        margin-right: 20px;
        align-self: flex-end;
    }
    @media only screen and (max-width: 700px) {
        &__cat {
            order: 2;
        }
    }
    .content__text {
        max-width: 425px;
    }
    .content__title {
        margin-top: 50px;
        font-size: 36px;
        color: $green;
    }
    p {
        margin-top: 20px;
        margin-bottom: 10px;
        line-height: 1.5;
        font-weight: 300;
    }
    .content__tips-link {
        padding: 2px 5px;
        font-size: 12px;
        color: #fff;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &__woman {
        position: absolute;
        right: 0;
    }
}

@media only screen and (max-width: 1079px) {
    .content {
        justify-content: center;
    }
    .content__woman {
        display: none;
    }
}

@import "media";